body{
    margin: 0;
    padding: 0;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f0f0;
}
div{
    text-align: center;
}
h1{
    color: #4caf50;
    font-family: Arial, Helvetica, sans-serif;
}
ul{
    list-style: none;
    padding: 0;
}
li{
    background-color: #f9f9f9;
    margin: 5px 0;
    padding: 10px;
    border-radius: 5px;
    font-family: sans-serif;
}
button{
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}
button:hover{
    background-color: #45a049;
}
input[type="text"]{
    padding: 10px;
    margin: 10px 0;
    border: 1px solid darkcyan;
    border-radius: 5px;
    width: 200px;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
}
input[type="text"]:focus{
    border-color: #4caf50;
    outline: none;
    box-shadow: 0 0 5px rgba(76 , 175, 80, 0.5);
}